import React from "react";

const About = () => {
  const stars = Array.from(Array(35).keys());

  return (
    <section id="about" className="about-section" data-scroll-section>
      <div className="nightsky-background">
        {stars.map((star) => (
          <div className="star" data-scroll data-scroll-repeat="true" id={star} key={star}></div>
        ))}
      </div>
      <div className="container">
        <div className="moon" data-scroll data-scroll-repeat="true"></div>
        <div className="text-container">
          <h2 data-scroll data-scroll-repeat="true">
            {" "}
            A propos{" "}
          </h2>
          <p data-scroll data-scroll-repeat="true">
            <br />
            Jeune reconverti dans la Tech de 34 ans. J'ai dcid de me spcialiser dans le dveloppement front-end. Mon
            but est de crer des sites web et des applications uniques et innovantes. Ma philosophie s'articule en 3
            points :
          </p>

          <p data-scroll data-scroll-repeat="true">
            <span data-scroll data-scroll-repeat="true">
              {" "}
              Comprendre
            </span>
            <br />
            Depuis l'enfance, j'ai toujours t curieux d'apprendre ce qui m'a conduit  m'intresser  une multitude de
            sujets, sciences et droit  travers mes tudes ou histoire et gnalogie  travers mes loisirs. Le
            dveloppement web ncessite un apprentissage continuel pour se perfectionner et j'en suis ravi.
          </p>
          <p data-scroll data-scroll-repeat="true">
            <span data-scroll data-scroll-repeat="true">
              {" "}
              Collaborer
            </span>
            <br />
            Je crois dans les vertues d'une bonne organisation, juste et honnte pour raliser des objectifs suprieurs.
            Qu'il s'agisse d'une socit, d'une quipe de dveloppement ou de la relation avec un client, l'quilibre,
            la confiance et la motivation sont primordiaux.
          </p>
          <p data-scroll data-scroll-repeat="true">
            <span data-scroll data-scroll-repeat="true">
              {" "}
              Crer
            </span>
            <br />
            Amener des sensations, transmettre de l'motion, impressionner ou apporter une nouveaut. Que ce soit par
            l'innovation ou la narration d'une histoire, pour moi crer est essentiel. Laisser quelque chose de
            diffrent c'est laisser une marque de son passage.
          </p>
        </div>
      </div>
    </section>
  );
};

export default About;
